import { mapState, mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'
import LoadingMore from '../../../components/loadingMoreTemplate'

export default {
  data () {
    return {
      upyunUrl,
      page: 1
    }
  },
  onLoad () {
    this.page = 1
    this.getMyAttentionListFn(true)
  },
  computed: {
    ...mapState([
      'myAttentionList'
    ])
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.page = 1
    this.getMyAttentionListFn(true)
  },
  // 上拉加载
  onReachBottom () {
    this.page += 1
    this.getMyAttentionListFn(false)
  },
  methods: {
    ...mapMutations({
      setMyAttentionList: 'SET_MY_ATTENTION_LIST'
    }),
    ...mapActions([
      'getMyAttentionList'
    ]),
    // 获取我的关注列表
    getMyAttentionListFn (loadingStatus) {
      const data = {
        that: this,
        page: this.page
      }
      if (loadingStatus) {
        uni.showLoading({ title: 'Loading' })
        this.setMyAttentionList([])
      }
      uni.showNavigationBarLoading()
      this.getMyAttentionList(data).then(() => {
        if (loadingStatus) uni.hideLoading()
        uni.stopPullDownRefresh()
        uni.hideNavigationBarLoading()
      })
    },
    onSkipPageFn (url) {
      uni.navigateTo({
        url
      })
    }
  },
  components: {
    LoadingMore
  }
}
